<template>
    <div id="login" :style="{height:fullHeight + 'px'}">
        <h3>growroomate</h3>
        <p class="tit">run your garden from your pocket</p>
        <div class="user">
            <input type="text" placeholder="Email" v-model="emailVal">
        </div>
        <div class="password">
            <input :type="showPassword?'password':'text'" placeholder="Password" v-model="password">
            <div class="eyes" @click="showPassword = !showPassword">
                <img src="../../assets/login/eye-close@3x.png" alt="" v-if="showPassword">
                <img src="../../assets/login/Eye-open@3x.png" alt="" v-if="!showPassword">
            </div>
        </div>
        <div class="select" @click="remember = !remember">
            <img src="../../assets/login/circle@3x.png" alt="" v-if="!remember">
            <img src="../../assets/login/Tickmark@3x.png" alt="" v-if="remember">
            <span>Remember my password</span>
        </div>
        <div :class="disabled?'button active': 'button'" @click="login">
            login
        </div>
        <div class="register" @click="goRegsister">
            <span>Register</span>
            <img src="../../assets/login/Next@3x.png" alt="">
        </div>
        <div class="forget" @click="goForgot()">
            <span>Forgot your password</span>
            <img src="../../assets/login/Next@3x.png" alt="">
        </div>
        <div class="language" :style="{marginBottom:keyboard?'-100px' : '0px'}">
            <div class="nav">Language</div>
            <div class="text">English</div>
        </div>
        <!-- 登录失败 -->
        <messageBox setHeight="190" setType="confirm" v-show="messageBoxType === 'loginFailed'" >
            <div slot="confirmColumn" class="common_alert">
                <div class="alert_icon">
                <span>Login Failed</span>
                </div>
                <div class="alert_describe">Email format error, please enter the correct Email !</div>
                <div class="alert_operation">
                <div class="determine_one" @click="alertChoose(0)" style="background:#cccccc;">OK</div>
                </div>
            </div>
        </messageBox>

        <transition mode="in-out" :name="transitionName">
          <router-view></router-view>
        </transition>
    </div>
</template>
<script>
import {setStore, getStore} from '@/utils/index'
import {mapActions, mapGetters, mapState} from 'vuex'
import messageBox from '../../components/common/messageBox'

import {
  emailLoginRequest
} from '../../api/method'
import {
  emailLoginResponse
} from '../../api/getData'
export default {
  data () {
    return {
      emailVal: null, // 帐号
      password: null,
      remember: false, // 默认不记住密码
      showPassword: true, // 默认不显示密码
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移)
      transitionName: null, // 页面过度动画
      loginStateus: true // 登录状态
    }
  },
  components: {
    messageBox
  },
  computed: {
    ...mapGetters(['messageType', 'mqttState']),
    disabled () {
      // 检测email 密码是否为空
      if (this.emailVal && this.password) {
        return true
      } else {
        return false
      }
    },
    messageBoxType () {
      return this.messageType
    }
  },
  mounted () {
    setTimeout(() => {
      let h = 0
      this.fullHeight = document.documentElement.clientHeight
      window.onresize = () => {
        h = document.documentElement.clientHeight
        // 软键盘是否弹起
        if (h < this.fullHeight) {
          this.keyboard = true
        } else {
          this.keyboard = false
        }
      }
    }, 100)
    let login = JSON.parse(getStore('login'))
    if (login) {
      if (login.remember) {
        this.remember = login.remember
        this.emailVal = login.email
        this.password = login.password
      }
    }
    emailLoginResponse(this.NAVIGATOR, this)
    console.log(this.$route)
  },
  methods: {
    ...mapActions(['showMessagesBox']),
    ...mapState(['ToastType']),
    goForgot () {
      // 忘记密码
      this.$router.push({path: '/login/forgot'})
    },
    goRegsister () {
      // 注册
      this.$router.push({path: '/login/register'})
    },
    login () {
      // 登录
      if (this.disabled) {
        emailLoginRequest(this.NAVIGATOR, this, this.emailVal, this.password)
        console.log(this.emailVal, this.password)
        if (!this.loginStateus) {
          this.showMessagesBox({'type': 'loginFailed'})
          return false
        }
        if (this.$root.ISDEBUG) {
          window.emailLoginResponse(true)
        }
        if (this.remember) {
          let login = {
            password: this.password,
            email: this.emailVal,
            remember: this.remember
          }
          setStore('login', login)
        } else {
          let login = {
            password: null,
            email: this.emailVal,
            remember: false
          }
          setStore('login', login)
        }
      }
    },
    alertChoose (num) {
      switch (num) {
        case 0:
          this.showMessagesBox({'type': null})
          break
        default:
          break
      }
    }

  },
  watch: {
    $route (to, from, next) {
      if (to.name === 'register') {
        this.transitionName = 'slide-up'
      } else {
        this.transitionName = 'slide-left'
      }
    }
  }
}
</script>
<style lang="less" scoped>
@import "../../styles/mixin.less";
#login{
    .wh(100%, 100%);
    background: url(../../assets/login/login-bg@3x.png) no-repeat;
    // background: #000;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fbfbf9;
    img {
        display: block;
    }
    h3{
        margin-top: 11.5%;
        color: white;
        font-size: 83px;
        font-weight: 400;
    }
    .tit{
        margin-top: 30px;
        font-size: 31px;
        color: white
    }
    .user, .password{
        .wh-l(490px, 96px);
        border:white solid  2px;
        border-radius: 10px 10px 10px 10px;
        margin-top: 11.4%;
        background: #c0cfba;
        padding: 0 24px;
        display: flex;
        input{
            font-size: 30px;
            background-color: transparent;
            .wh-l(100%, 100%);
            color: #fbfbf9;
        }
        input::-webkit-input-placeholder { /* WebKit browsers */
            color: white;
        }
        input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
            color: white;
        }
        input::-moz-placeholder { /* Mozilla Firefox 19+ */
            color: white;
        }
        input:-ms-input-placeholder { /* Internet Explorer 10+ */
            color: white;
        }
    }
    .password{
        margin-top: 60px;
        display: flex;
        justify-content: space-between;
        .eyes{
            .wh(40px, 32px);
            align-self: center;
        }
        img{
            .wh(40px, 32px);
        }
    }
    .select{
        display: flex;
        margin-top: 28px;
        width: 490px;
        justify-content: flex-start;
        img{
            .wh(34px, 34px);
            margin-right: 20px;
        }
        span{
            font-size: 25px;
            color: #fbfbf9;
        }
    }
    .button{
        .wh-l(490px, 96px);
        border: 2px solid white;
        margin-top: 15.7%;
        box-sizing: content-box;
        background: #D6D6D6;
        opacity: 0.7;
    }
    .active{
        background: #13af68;
        opacity: 1;
    }
    .register, .forget {
        height: 30px;
        margin-top: 60px;
        width: 490px;
        line-height: 30px;
        display: flex;
        justify-content: flex-end;
        font-size: 26px;
        img{
            .wh(26px,26px);
            margin-left: 20px;
            align-self: center;
        }
    }
    .forget{
        margin-top: 40px;
    }
    .language{
        display: flex;
        justify-content: center;
        // margin-top: 193px;
        position: absolute;
        bottom: 50px;
        left: 50%;
        margin-left: -125px;
        font-size: 27px;
        .nav{
            margin-right: 42px;
        }
    }
}
</style>



